<template lang="html">
  <div id="view-truck">
    <ul class="collection with-header" :class="{'green-text' : (truckAvailable==true), 'red-text' : (truckAvailable==false)}" style="text-transform:uppercase">
      <li class="collection-header"><h4>{{truckUnit}}</h4></li>
      <li class="collection-item" disabled><span class="grey-text">VIN: </span>{{truckVin}}</li>
      <li class="collection-item"><span class="grey-text">Key Location: </span>{{keyLoc}}</li>
      <li class="collection-item"><span class="grey-text">Year: </span>{{truckYear}}</li>
      <li class="collection-item"><span class="grey-text">Model: </span>{{truckModel}}</li>
      <li class="collection-item"><span class="grey-text">Truck Location: </span>{{truckLoc}}</li>
      <li class="collection-item"><span class="grey-text">Clean: </span>{{clean}}</li>
      <li class="collection-item"><span class="grey-text">Available: </span>{{truckAvailable}}</li>
    </ul>
    <router-link to="/" class="btn grey">Back</router-link>
    <button @click="deleteTruck" class="btn red">Delete</button>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>

    <div class="fixed-action-btn">
      <router-link v-bind:to="{ name: 'edit-truck', params: { truckVin: truckVin }}" class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>

</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-truck',
  data () {
    return {
      docID: null,
      truckID: null,
      clean: null,
      keyLoc: null,
      truckAvailable: null,
      truckLoc: null,
      truckModel: null,
      truckUnit: null,
      truckVin: null,
      truckYear: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('trucks').where('truckVin', '==', to.params.truckVin).get().then((querySnapshot) => {
      querySnapshot.forEach((doc) => {
        next(vm => {
          vm.clean = doc.data().clean
          vm.keyLoc = doc.data().keyLoc
          vm.truckAvailable = doc.data().truckAvailable
          vm.truckLoc = doc.data().truckLoc
          vm.truckModel = doc.data().truckModel
          vm.truckUnit = doc.data().truckUnit
          vm.truckVin = doc.data().truckVin
          vm.truckYear = doc.data().truckYear
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('trucks').where('truckVin', '==', this.$route.params.truckVin).get().then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          this.truckVin = truckVin
        })
      })
    },
    deleteTruck () {
      if(confirm ('Are you sure?')) {
        db.collection('trucks').where('truckVin', '==', this.$route.params.truckVin).get().then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            doc.ref.delete();
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>

<style lang="css">
</style>



